import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import '../css/Header.css'

const Header = () => {

    const [widthScreen, setWidthScreen] = useState('');
    const [mobilClass, setMobilClass] = useState('');

    const mobile = () => {
        setWidthScreen(window.innerWidth)
    };

    console.log(widthScreen);

    useEffect(() => {
        mobile()
        widthScreen < 768 ? setMobilClass("position-relative w-100") : setMobilClass("")
    }, [widthScreen]);

    return (
        <div className="header-top bg-primary">
            <nav class="navbar navbar-expand-lg navbar-dark">
                <Link class="navbar-brand text-white font-weight-bold" to="/">Tramitero.com</Link>
                <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavDropdown" aria-controls="navbarNavDropdown" aria-expanded="false" aria-label="Toggle navigation">
                    <span class="navbar-toggler-icon"></span>
                </button>
                <div class="collapse navbar-collapse justify-content-center" id="navbarNavDropdown">
                    <ul class="navbar-nav w-100">
                        <li class={`nav-item`}>
                            <Link class="nav-link dropdown-toggle text-white" id="Anses" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                Anses
                            </Link>
                            <div class={`dropdown-content ${mobilClass}`} aria-labelledby="Anses">
                                <Link class="dropdown-item" to="/Anses/prestamos-jubilados-y-pensionados">Prestamos Jubilados y Pensionados</Link>
                                <Link class="dropdown-item" to="/Anses/prestamos-auh-y-suaf">Prestamos AUH y SUAF</Link>
                                <Link class="dropdown-item" to="/Anses/tarjeta-alimentaria">Tarjeta Alimentaria</Link>
                                <Link class="dropdown-item" to="/Anses/fondo-de-desempleo">Fondo de Desempleo</Link>
                                <Link class="dropdown-item" to="/Anses/asignacion-familiar-por-hijo">Asignación Familiar por Hijo</Link>
                                <Link class="dropdown-item" to="/Anses/asignacion-universal-por-hijo">Asignación Universal por Hijo</Link>
                            </div>
                        </li>
                        <li class={`nav-item`}>
                            <Link class="nav-link dropdown-toggle text-white" to="#" id="Afip" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                Afip
                            </Link>
                            <div class={`dropdown-content ${mobilClass}`} aria-labelledby="Afip">
                                <Link class="dropdown-item" to="/Afip/clave-fiscal">Clave Fiscal</Link>
                                <Link class="dropdown-item" to="/Afip/formulario-ceta">Formulario CETA</Link>
                                <Link class="dropdown-item" to="/Afip/factura-electronica">Factura Electrónica</Link>
                                <Link class="dropdown-item" to="/Afip-inscripción-monotributo">Incripción al Monotributo</Link>
                                <Link class="dropdown-item" to="/Afip/turnos-afip">Turnos Afip</Link>
                                <Link class="dropdown-item" to="/Afip/generar-vep">Generar VEP</Link>
                            </div>
                        </li>
                        <li class={`nav-item`}>
                            <Link class="nav-link dropdown-toggle text-white" id="Renaper" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                Renaper
                            </Link>
                            <div class={`dropdown-content ${mobilClass}`} aria-labelledby="Renaper">
                                <Link class="dropdown-item" to="/Renaper/tramite-dni">Trámite DNI</Link>
                                <Link class="dropdown-item" to="/Renaper/tramite-pasaporte">Trámite Pasaporte</Link>
                                <Link class="dropdown-item" to="/Renaper/certificado-nacimiento">Cert. Nacimiento</Link>
                                <Link class="dropdown-item" to="/Renaper/certificado-casamiento">Cert. Casamiento</Link>
                                <Link class="dropdown-item" to="/Renaper/certificado-de-defuncion">Cert. de Defunción</Link>

                            </div>
                        </li>
                        <li class={`nav-item`}>
                            <Link class="nav-link dropdown-toggle text-white" id="Automotor" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                Automotor
                            </Link>
                            <div class={`dropdown-content ${mobilClass}`} aria-labelledby="Automotor">
                                <Link class="dropdown-item" to="/Automotor/turno-registro-automotor">Turno Registro Automotor</Link>
                                <Link class="dropdown-item" to="/Automotor/transferencia-automotor">Transferencia Automotor</Link>
                                <Link class="dropdown-item" to="/Automotor/patentamiento-automotor">Patentamiento Automotor</Link>
                                <Link class="dropdown-item" to="/Automotor/titulo-automotor">Titulo Automotor</Link>
                                <Link class="dropdown-item" to="/Automotor/tramite-cedula-azul">Trámite Cedula Azul</Link>
                            </div>
                        </li>
                    </ul>
                    <form class="form-inline my-2 my-lg-0">
                        <input class="form-control" type="search" placeholder="Buscar" aria-label="Buscar" />
                    </form>
                </div>
            </nav>
        </div>
    )
};

export default Header;